import React from 'react';
import MoveList from '../../user/components/MovesList'
import ContactPreview from '../components/ContactPreview'
import TransferFund from '../components/TransferFund'
import { Link } from 'react-router-dom'
import { loadContact } from '../actions'
import { addMove } from '../../user/actions'
import { connect } from 'react-redux'
import Fab from '@material-ui/core/Fab';
import EditIcon from '@material-ui/icons/Edit';

class ContactDetails extends React.Component {

    transferFund = (ev, amount) => {
        ev.persist()
        ev.preventDefault()
        const move = {
            amount,
            to: this.props.contact
        }
        this.props.addMove(move)
    }
    async componentDidMount() {
        const { id } = this.props.match.params
        this.props.loadContact(id)
    }
    render() {
        let { contact, user } = this.props
        return (
            <div className="ContactDetails container">
                <Link to={`/contact`} className="BackLink">
                    <button>Back</button>
                </Link>
                {contact && <ul className="ContactPreviewTransfer">
                    <Link to={`/contact/edit/${contact._id}`}>
                            <EditIcon />
                    </Link>

                    <ContactPreview contact={contact} />
                    <TransferFund contact={contact} TransferFund={this.transferFund} >
                        <MoveList moves={user.moves.filter(move => move.to.name === contact.name)} />
                    </TransferFund>
                </ul>
                }

            </div>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        contact: state.contact.currContact,
        user: state.user.user
    }
}

const mapDispatchToProps = {
    loadContact,
    addMove
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ContactDetails)